<template>
  <router-view id="router-view" />
</template>

<script>
export default {
  mounted() {
    document.title = "dvegasa";
  },
  watch: {
    $route: {
      immediate: true,
      handler(to) {
        document.title = to.meta.title || "dvegasa";
      },
    },
  },
};
</script>

<style lang="scss">
@import "@/styles";

#router-view {
  width: 100%;
  height: 100%;
}
</style>
